<template>
    <div class="report-form">
        <!-- <div class="game-nav">
            <div :class="['nav-item', activeGameType == item.key ? 'active' : '']" v-for="(item, index) in gameBigList" :key="'game-type-' + index" @click="handleSelectGameBigType(item)">
                <span>{{item.title}}</span>
            </div>
        </div> -->
        <div class="content">
            <div class="query-list">
                <div class="query-list-t">
                    <div class="query-item">
                        <span>快捷：</span>
                        <div class="quick-method">
                            <span :class="quickSelect === 1 ? 'active' : ''" @click="handleQueryByDate(1)">今天</span>
                            <span :class="quickSelect === 2 ? 'active' : ''" @click="handleQueryByDate(2)">昨天</span>
                            <span :class="quickSelect === 3 ? 'active' : ''" @click="handleQueryByDate(3)">近一周</span>
                            <span :class="quickSelect === 4 ? 'active' : ''" @click="handleQueryByDate(4)">自定义</span>
                        </div>
                    </div>
                    <i-select v-model="query.gameType" style="width: 124px;" @on-change="handleChangeType">
                        <i-option v-for="(item, index) in gameList" :key="index" :value="item.type">{{item.name}}</i-option>
                    </i-select>
                    <i-select v-model="query.selectUserType" @on-change="handleChangeType" style="width: 124px;">
                        <i-option v-for="(item, index) in userTypeList" :key="index" :value="item.value">{{item.title}}</i-option>
                    </i-select>
                    <div class="query-button" @click="handleChangeType">查询</div>
                </div>
                <div class="query-list-t">
                    <div class="quick-date">
                        <span>区间：</span>
                        <DatePicker v-model="query.startTime" :disabled="canEdit" type="date" class="date-item"></DatePicker>
                        <span>06:00 --</span>
                        <DatePicker v-model="query.endTime" :disabled="canEdit" type="date" class="date-item"></DatePicker>
                        <span>06:00</span>
                    </div>
                    <i-input placeholder="查询用户ID/用户名" class="query-input-item" v-model="query.searchUsername"></i-input>
                    <div class="query-button delete" @click="showDelete = true;">删除记录</div>
                </div>
            </div>
            <div class="query-operate">
                <i-select v-model="query.gameModeType" @on-change="handleChangeMode">
                    <i-option v-for="(item, index) in gameTypeList" :key="index" :value="item.value">{{item.title}}</i-option>
                </i-select>
                <span @click="handleBackwater">一键回水</span>
            </div>
            <div class="query-poptip">
                <p>1、一键回水流程是先根据左侧条件查询和回水模式筛选的选择在列表呈现需回水用户，然后点击一键回水执行列表用户的回水操作。</p>
                <p>2、点击一键回水会根据预设方案执行回水操作。需要注意的是回水只能计算某一天，如选择多天则无法成功回水。</p>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>用户报表</span>
                    <div class="list-poptip">
                        <span>玩家总数</span>
                        <span>{{allGamer}}</span>
                        <span>盈亏合计：</span>
                        <span>{{lossAndWinnerAll}}</span>
                        <span>流水合计：</span>
                        <span>{{flowWaterAll}}</span>
                        <span>回水合计：</span>
                        <span>{{backwaterAll}}</span>
                    </div>
                </div>
            </div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info item-1">用户名</p>
                    <p class="item-info item-2">ID</p>
                    <p class="item-info item-3">用户类型</p>
                    <p class="item-info item-4">游戏</p>
                    <p class="item-info item-5">流水</p>
                    <p class="item-info item-6">盈亏</p>
                    <p class="item-info item-7">回水方案</p>
                    <p class="item-info item-8">回水金额</p>
                    <p v-if="showDataList.length && showDataList[0].reversible !== null" class="item-info item-9">可返流水</p>
                    <p v-if="showDataList.length && showDataList[0].reversible !== null" class="item-info item-10">状态</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in showDataList" :key="index">
                        <p class="item-info item-1">{{item.username}}</p>
                        <p class="item-info item-2">{{item.showId}}</p>
                        <p class="item-info item-3">{{item.userType}}</p>
                        <p class="item-info item-4">{{item.gamaName}}</p>
                        <p class="item-info item-5">{{item.flowingWater}}</p>
                        <p class="item-info item-6">{{item.profitLoss}}</p>
                        <p class="item-info item-6">{{item.backType}}</p>
                        <p class="item-info item-8">{{item.backwater}}</p>
                        <p v-if="item.reversible !== null" class="item-info item-9">{{item.reversible}}</p>
                        <p v-if="item.reversible !== null" class="item-info item-10" :style="{width: showDataList.length <= 12 ? '97px' : 'auto'}">{{item.status}}</p>
                    </div>
                </div>
            </div>
        </div>
        <!-- <loading :loading="showLoading"></loading> -->
        <Modal v-model="showDelete" :styles="{top: '200px'}" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>确认删除记录</span>
            </p>
            <p style="text-align:center">是否确认删除记录？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="showDelete = false">取消</div>
                <div class="enter-ok" @click="handleEnterCancel">确定</div>
            </div>
        </Modal>
    </div>
</template>

<script>
export default {
    data() {
        return {
            activeGameType: 'lottery',
            gameBigList: [
                {
                    title: '彩票游戏',
                    key: 'lottery'
                },
                {
                    title: '视讯游戏',
                    key: 'video'
                }
            ],
            showLoading: false,
            quickSelect: 1,
            canEdit: true,
            showDelete: false,
            query: {
                startTime: new Date().getHours() >= 6 ? new Date() : new Date(new Date().setDate(new Date().getDate() - 1)),
                endTime: new Date(new Date().setDate(new Date().getDate() + 1)),
                searchUsername: '',
                gameType: '0',
                selectUserType: '1',
                gameModeType: 0
            },
            gameList: [],
            selectGameActive: 'lottery',
            gameType: [
                {
                    value: 'lottery',
                    title: '彩票游戏'
                },
                {
                    value: 'video',
                    title: '视讯游戏'
                }
            ],
            userTypeList: [
                // {
                //     title: '所有用户',
                //     value: 0
                // },
                {
                    title: '正式用户',
                    value: '1'
                },
                {
                    title: '虚假用户',
                    value: '4'
                }
            ],
            gameTypeList: [
                {
                    title: '所有玩家',
                    value: 0
                },
                {
                    title: '按流水回水玩家',
                    value: 1
                },
                {
                    title: '按输赢回水玩家',
                    value: 2
                }
            ],
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            dataList: [],
            showDataList: [],
            forbiddenTime: {
                disabledDate (date) {
                    return date && date.valueOf() >= Date.now();
                }
            },
            isLoading: false,
            loginName: '',
            isDeleting: false,
            selectItem: {}
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            let loginName = localStorage.getItem('loginName');

            if (loginName) {
                vm.loginName = loginName;
                vm.getGameList().then(function() {
                    vm.handleGetData();
                })
            }
        })
    },
    computed: {
        allGamer: function() {
            let that = this,
                vList = [];
            that.showDataList.forEach(item => {
                if (vList.findIndex(ele => ele === item.showId) === -1) {
                    if (item.userType === '正常')
                        vList.push(item.showId);
                }
            })
            return vList.length;
        },
        lossAndWinnerAll: function() {
            let that = this;

            return that.showDataList.reduce((total, item) => total += (item.userType === '正常' ? item.profitLoss : 0), 0).toFixed(2);
        },
        flowWaterAll: function() {
            let that = this;

            return that.showDataList.reduce((total, item) => total += (item.userType === '正常' ? item.flowingWater : 0), 0).toFixed(2);
        },
        backwaterAll: function() {
            let that = this;

            return that.showDataList.reduce((total, item) => total += (item.userType === '正常' ? item.backwater : 0), 0).toFixed(2);
        }
    },
    methods: {
        handleSelectGameBigType(item) {
            let that = this;

            that.activeGameType = item.key;
            that.page.pageNum = 1;
            that.getGameList().then(function() {
                that.handleGetData();
            })
        },
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                if (that.activeGameType == 'lottery') {
                    that.$axios({
                        url: '/api/Merchant/GetGameList'
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            const result = data.data.Data;

                            that.gameList = result.map(item => {
                                return {
                                    name: item.GameName,
                                    type: item.GameType,
                                    key: item.NickName
                                }
                            }).sort((a, b) => a.type - b.type);
                            that.gameList.unshift({
                                name: '所有游戏',
                                type: '0',
                                key: 'all'
                            })
                            resolve();
                        }
                        reject();
                    })
                } else {
                    that.gameList = [];
                    that.gameList.push({
                        name: '所有游戏',
                        type: '0',
                        key: 'all'
                    });
                    that.gameList.push({
                        name: '百家乐',
                        type: '1',
                        key: 'bjl'
                    });
                    resolve();
                }
            })
        },
        handleEnterCancel() {
            let that = this;

            that.showDelete = false;
            that.handleDelete();
        },
        handleQueryByDate(type) {
            let that = this,
                dateNow = new Date().getHours() >= 6 ? new Date() : new Date(new Date().setDate(new Date().getDate() - 1)),
                afterDate = new Date(new Date().setDate(new Date().getDate() + 1)),
                beforeDate = new Date(new Date().setDate(new Date().getDate() - 1)),
                beforeSeven = new Date(new Date().setDate(new Date().getDate() - 6));

            that.quickSelect = type;
            switch(type) {
                case 1:
                    that.canEdit = true;
                    that.query.startTime = dateNow;
                    that.query.endTime = afterDate;
                    that.handleGetData(dateNow.pattern('yyyy-MM-dd') + ' 06:00', afterDate.pattern('yyyy-MM-dd') + ' 06:00');
                break;
                case 2:
                    that.canEdit = true;
                    that.query.startTime = beforeDate;
                    that.query.endTime = dateNow;
                    that.handleGetData(beforeDate.pattern('yyyy-MM-dd') + ' 06:00', dateNow.pattern('yyyy-MM-dd') + ' 06:00');
                break;
                case 3:
                    that.canEdit = true;
                    that.query.startTime = beforeSeven;
                    that.query.endTime = afterDate;
                    that.handleGetData(beforeSeven.pattern('yyyy-MM-dd') + ' 06:00', afterDate.pattern('yyyy-MM-dd') + ' 06:00');
                break;
                case 4:
                    that.canEdit = false;
                break;
            }
        },
        handleChangeType() {
            let that = this;

            that.handleGetData();
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.handleGetData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.handleGetData();
        },
        handlechangeColl(item) {
            let that = this;

            if (item.value.length > 1) {
                that.handleGetData(item.type);
            }
        },
        handleBackwater() {
            let that = this,
                param = {},
                _url = '';

            if (!that.query.startTime && !startTime) {
                that.$Message.success('请选择开始时间');
                return;
            }

            if (!that.query.endTime && !endTime) {
                that.$Message.success('请选择结束时间');
                return;
            }

            param['startTime'] = that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            param['endTime'] = that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';

            if (that.query.gameModeType) {
                param['pattern'] = that.query.gameModeType;
            }

            if (that.query.searchUsername) {
                param['userKeyword'] = that.query.searchUsername;
            }

            if (that.query.selectUserType) {
                param['userType'] = that.query.selectUserType;
            }

            if (that.query.gameType != '0') {
                param['gameType'] = that.query.gameType;
            }
            _url = that.activeGameType == 'lottery' ? '/api/Monito/GhostReport' : '/api/Monito/GhostVideoReport';
            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                that.$axios({
                    url: _url,
                    method: 'post',
                    params: param
                }).then(data => {
                    that.isLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.showLoading = false;
                    that.handleGetData();
                })
            }
        },
        handleDelete() {
            let that = this,
                param = {},
                _url = '';

            if (!that.query.startTime && !startTime) {
                that.$Message.success('请选择开始时间');
                return;
            }

            if (!that.query.endTime && !endTime) {
                that.$Message.success('请选择结束时间');
                return;
            }

            param['startTime'] = that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            param['endTime'] = that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';

            if (that.query.gameModeType) {
                param['pattern'] = that.query.gameModeType;
            }

            if (that.query.searchUsername) {
                param['userKeyword'] = that.query.searchUsername;
            }

            if (that.query.selectUserType) {
                param['userType'] = that.query.selectUserType;
            }

            if (that.query.gameType != '0') {
                param['gameType'] = that.query.gameType;
            }
            _url = that.activeGameType == 'lottery' ? '/api/Monito/DeleteRecords' : '/api/Monito/DeleteVideoRecords';
            if (!that.isDeleting) {
                that.isDeleting = true;
                that.showLoading = true;

                that.$axios({
                    url: _url,
                    method: 'post',
                    params: param
                }).then(data => {
                    that.isDeleting = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }

                    that.showLoading = false;
                    that.handleGetData();
                })
            }
        },
        handleBackwaterType(item) {
            let that = this,
                param = {};

            if (!that.query.startTime || !that.query.endTime) {
                that.$Message.success('请选择查询日期');
                return;
            }

            if (that.query.searchUsername) {
                param['userCode'] = that.query.searchUsername;
            }

            param['startTime'] = that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            param['endTime'] = that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';
            param['pattern'] = that.query.gameModeType;
            param['gameType'] = item.type;

            if (!item.isLoading) {
                item.isLoading = true;

                that.$axios({
                    url: '/api/Monito/GhostReportByGameType',
                    method: 'post',
                    params: param
                }).then(data => {
                    item.isLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.handleGetData(item.type);
                })
            }
        },
        handleGetData(startTime, endTime) {
            let that = this,
                param = {},
                _url = '';

            if (!that.query.startTime && !startTime) {
                that.$Message.success('请选择开始时间');
                return;
            }

            if (!that.query.endTime && !endTime) {
                that.$Message.success('请选择结束时间');
                return;
            }

            param['startTime'] = startTime || that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            param['endTime'] = endTime || that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';
            that.query.gameModeType = 0;

            if (that.query.searchUsername) {
                param['userKeyword'] = that.query.searchUsername;
            }

            if (that.query.selectUserType) {
                param['userType'] = that.query.selectUserType;
            }

            if (that.query.gameType != 0) {
                param['gameType'] = that.query.gameType;
            }
            _url = that.activeGameType == 'lottery' ? '/api/Monito/SearchReport' : '/api/Monito/SearchVideoReport';
            if (!that.showLoading) {
                that.showLoading = true;

                that.$axios({
                    url: _url,
                    params: param
                }).then(data => {

                    if (data.data.Status === 100) {
                        const result = data.data.Data;
                        that.dataList = result.map(item => {
                            return {
                                username: item.NickName,
                                showId: item.OnlyCode,
                                userType: item.UserStatus == 1 ? '正常' : '虚拟',
                                gamaName: that.gameList.find(ele => ele.type == item.GameType).name,
                                flowingWater: item.InputAmount,
                                profitLoss: item.ProLoss,
                                backType: item.SchemeName,
                                backwater: item.Ascent,
                                status: item.BackStatus == 1 ? '已返水' : (item.BackStatus == 2 ? '未返水' : item.BackStatus == 3 ? '可返水' : ''),
                                mode: item.Pattern,
                                reversible: item.Reversible
                            }
                        })

                        that.handleChangeMode();
                    }
                    that.showLoading = false;
                })
            }
        },
        handleChangeMode() {
            let that = this;

            switch(that.query.gameModeType) {
                case 0:
                    that.showDataList = that.dataList;
                break;
                case 1:
                    that.showDataList = that.dataList.filter(item => item.mode === that.query.gameModeType);
                break;
                case 2:
                    that.showDataList = that.dataList.filter(item => item.mode === that.query.gameModeType);
                break;
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.report-form {
    position: relative;

    .game-nav {
        display: flex;
        align-items: stretch;
        margin: 10px 0;
        height: 36px;
        background-color: #f2f2f2;
        border-radius: 4px;
        overflow: hidden;

        .nav-item {
            padding: 0 20px;

            line-height: 36px;

            cursor: pointer;
        }

        .active {
            background-color: rgb(65, 199, 219);
        }
    }

    .content {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        .query-list {
            padding: 10px;

            background-color: rgb(228, 228, 228);
            border: 1px solid rgb(215, 215, 215);

            .query-list-t {
                display: flex;
                align-items: center;

                margin-top: 14px;

                & > * {
                    margin-right: 13px;

                    text-align: left;
                }

                .query-item {
                    display: flex;
                    align-items: center;

                    & > span {
                        flex-shrink: 0;

                        width: 42px;
                    }
                }

                .query-input-item {
                    flex-shrink: 0;

                    width: 223px;
                }

                .query-button {
                    margin-right: 0;

                    width: 109px;
                    height: 28px;
                    line-height: 24px;

                    // background-color: rgb(204, 204, 204);
                    // border-radius: 4px;
                    // border: solid 1px rgb(161, 161, 161);
                    background-color: #0078ff;
                    box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                    border-radius: 10px;
                    border: solid 1px #0e3c90;
                    text-align: center;
                    cursor: pointer;
                    color: #fff;
                }

                .delete {
                    // background-color: rgb(255, 153, 153);
                    // border-color: rgb(255, 102, 153);
                    background-color: #a30d26;
                    box-shadow: inset 0px 0px 5px 0px rgba(255, 124, 124, 0.75);
                    border-radius: 10px;
                    border: solid 1px #900e0e;
                }

                .quick-method {
                    display: flex;
                    align-items: center;

                    width: 270px;
                    height: 30px;
                    line-height: 30px;

                    border: solid 1px rgb(174, 174, 174);
                    text-align: center;

                    & > span {
                        flex: 1 1 25%;

                        background-color: #fff;
                        cursor: pointer;
                    }

                    .active {
                        background-color: rgb(65, 199, 219);
                    }
                }

                .quick-date {
                    .date-item {
                        width: 110px;
                    }
                }
            }

            .query-list-t:first-child {
                margin-top: 0;
            }
        }

        .query-operate {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            margin-left: 13px;
            padding: 10px;

            width: 176px;
            height: 100px;

            background-color: rgb(228, 228, 228);
            border: 1px solid rgb(215, 215, 215);
            box-sizing: border-box;

            & > span {
                display: inline-block;
            }

            & > span {
                height: 28px;
                line-height: 24px;

                // background-color: rgb(204, 204, 204);
                // border-radius: 4px;
                // border: solid 1px rgb(161, 161, 161);
                background-color: #5b55d9;
                box-shadow: inset 0px 0px 5px 0px rgba(127, 124, 255, 0.75);
                border-radius: 10px;
                border: solid 1px #0e2390;
                text-align: center;
                cursor: pointer;
                color: #fff;
            }
        }

        .query-poptip {
            margin-top: 10px;
            padding-left: 6px;

            width: 100%;

            background-color: rgb(228, 228, 228);
            border: 1px solid rgb(215, 215, 215);
            box-sizing: border-box;
            & > p {
                height: 22px;

                color: rgb(255, 102, 0);
                text-align: left;
            }
        }

        .list-header {
            width: 100%;

            .list-title {
                justify-content: space-between;

                width: 100%;

                .list-poptip {
                    width: 682px;
                    height: 30px;
                    line-height: 30px;

                    border-radius: 2px;
                    background-color: rgb(228, 228, 228);
                    border: 1px solid rgb(201, 201, 201);

                    & > span {
                        display: inline-block;

                        font-weight: 400;
                    }

                    & > span:nth-child(2n+1) {
                        width: 70px;

                        text-align: right;
                    }

                    & > span:first-child {
                        width: 110px;

                        text-align: center;
                    }

                    & > span:nth-child(2n+2) {
                        width: 85px;

                        color: rgb(51, 153, 102);
                    }

                    & > span:nth-child(2) {
                        color: rgb(255, 102, 51);
                    }

                    & > span:last-child {
                        color: rgb(51, 51, 51);
                    }
                }
            }
        }

        .footer-page {
            width: 100%;
